@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin:0;
  padding:0;
  font-family: 'Poppins', sans-serif;
}

.App {
    display: block;
    max-width: 1400px;
    margin-left: auto;
    margin-right: auto;
}
.a {
    height: 100vh;
    display: flex;
    align-items: center;
}

.a-left {
    flex:1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-card.bg {
    position: absolute;
    top:50;
    left: 10;
    background: rgb(244, 244, 244);
}
.a-right {
    flex:1 1;
}

.a-card {
    width: 80%;
    height: 50vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.a-img {
    width: 100%;
    height: 100%;
    border-radius: 30px;
    object-fit: contain;
}

.a-title {
    font-weight: bold;
}

.a-sub {
    margin:20px 0;
    font-size: 1.2rem;
}

.a-desc {
    font-weight: 300;
    margin-right: 60px;
    color:rgb(135, 132, 132);
}

.a-stack {
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.a-stack-item {
    margin: 0 0.5em;
}
.award-texts {
    width: 70%;
}

.a-award-title {
    font-weight: bold;
    color: #13013fa5;
    margin-bottom: 10px;
}

@media screen and (max-width: 480px) {
    .a {
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    }

    .a-left {
        width: 100%;
    }

    .a-card {
        height: 30vh;
    }

    .a-card.bg {
        display: none;
    }

    .a-right {
        padding: 20px;
    }

    .a-stack {
        display: none;
    }

    .a-desc {
        margin-right: 0;
    }
}

.contact {
    height: 100vh;
    position: relative;
    display: flex;
    align-items: center;
}

.contact-bg {
    width: 20px;
    height: 100%;
    background-color: #13013fa5;
    position: absolute;
}

.contact-title {
    font-size: 60px;
    width: 80%;
}

.contact-wrapper {
    padding:50px;
    display: flex;
    align-items: center;
}

.contact-left {
    flex: 1 1;
}
.contact-right {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.contact-info-item {
    display: flex;
    align-items: center;
    margin: 50px 0;
    font-weight: 300;
    width: 70%;
}

.contact-icon {
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

.contact-description {
    font-weight: 200;
}

form {
    margin-top: 20px;

}

input {
    width: 50%;
    height: 50px;
    border:none;
    border-bottom: 1px solid #eee;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

textarea {
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

button {
    border: none;
    padding: 15px;
    background-color: #13013fa5;
    color: white;
    font-weight: 500;
    cursor: pointer;
}

@media screen and (max-width: 480px) {
    .contact-wrapper {
        flex-direction: column;
    }
    .contact-bg {
        height: 100%;

    }
}
.i {
    display: flex;
    height:100vh;
}

.i-left {
    flex:1 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.i-left-wrapper{
    padding:50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.i-right {
    flex:1 1;
    position: relative;
}

.i-intro {
    font-size: 30px;
    font-weight: 300;
}

.i-name {
    font-size: 60px;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color:#472896;
    display: flex;
    align-items: center;
    text-align: center;
}

@keyframes move {
    25% {
        transform: translateY(-50px);
    }
    50% {
        transform: translateY(-100px);
    }
    75% {
        transform: translateY(-150px);
    }
    100% {
        transform: translateY(-200px);
    }
}

.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
    text-align: center;
}

.coding-img {
    width: 100%;
    height: 100%;
}

.i-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.i-bg {
    -webkit-clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
            clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
    background-color: #eee;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}

@media screen and (max-width: 480px) {
    .i {
        flex-direction: column;
    }

    .i-left-wrapper {
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }

    .i-desc {
        display: none;
    }

    .i-title-wrapper {
        justify-content: center;
        text-align: center;
    }
    
    .i-title-item {
        text-align: center;
    }
}
.project-container { 
    height: 60vh;
    display: flex;
}

.project-text-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    padding: 2em;
}

.project-item {
    padding: 1em 0;
}

.project-image-container {
    display: flex;
    justify-content: center;
    overflow: hidden;
    align-items: center;
}

.project-image { 
    width: 100%;
    height: 40%;
}

.c {
    width:45%;
    height: auto;
    margin: 20px 10px;
    border: 2px solid #eee;
    border-radius: 10px 10px 0 0;
}

.c-browser { 
    background-color: #eee;
    display: flex;
    align-items: center;
    z-index: 2;
}

.c-circle {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin-left: 10px;
    background-color: white;
}

.red {
    background-color: red;
}

.yellow{
    background-color: orange;
}

.green {
    background-color: green;
}

.c-img {
    width: 100%;
}

@media screen and (max-width: 460px) {
    .c {
        width: 100%;
        margin: 10px 0px;
        padding: 10px;
        height: 400px;
    }
    .project-container {
        flex-direction: column;
    }
    
    .project-image {
        width: 100%;
    }

    .c-browser {
        width: 100%;
        height: 20px;
    }

    .project-text-container {
        text-align: center;
        justify-content: center;
        display: flex;
        margin: 0;
        padding:0;
        width: 100%;
    }

    .project-title {
        text-align: center;
        
    }
}
